import React, { useState } from "react";
import "./Detailed.scss";
import dubai from "../../../assets/Images/dubai.jpg";
import { DetaileDate } from "./DetailedData";
import { Ri24HoursFill } from "react-icons/ri";
import { GoCalendar } from "react-icons/go";
import { IoManSharp } from "react-icons/io5";
import travel from "../../../assets/Images/TRAVELSYSTEM.png";
import { Tab, TabList, TabPanel, Tabs } from "react-tabs";
import Istanbul from "../../../assets/Images/istanbul.jpg";
import kappadokiya from "../../../assets/Images/kappadokija.jpg";
import { LazyLoadImage } from "react-lazy-load-image-component";
import item1 from "../../../assets/Images/item1.jpg";
import item2 from "../../../assets/Images/item2.jpg";
import item3 from "../../../assets/Images/item3.jpg";
import item4 from "../../../assets/Images/item4.jpg";
import Footer from "../../Footer/Footer";
import Modal from "react-modal";
import { Close } from "../../../assets/Icons";

const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
    zIndex: "9999",
    width: "25%",
  },
};

const Detailed = () => {
  const [activeTab, setActiveTab] = useState(0);
  const [openDate, setOpenDate] = useState(true);
  const [openDate2, setOpenDate2] = useState(false);
  const [modalIsOpen, setIsOpen] = React.useState(false);
  let subtitle;

  const handleLink = (i) => {
    setActiveTab(i);
    setOpenDate(!openDate);
    setOpenDate2(!openDate2);
  };

  function openModal() {
    setIsOpen(true);
  }

  function afterOpenModal() {
    subtitle.style.color = "#f00";
  }

  function closeModal() {
    setIsOpen(false);
  }

  return (
    <>
      <div className="super_container">
        <div className="detailed-con">
          <div
            className="detailed-background-image parallax-window"
            data-parallax="scroll"
            style={{ backgroundImage: `url(${dubai})` }}
          >
            <div className="detailed_content-header">
              <div className="detailed_title-birnchi">
                <h1>BAAda All inslusive</h1>
                <h3>
                  BAAga sayohatlar 2021-2022 | BAAda dam olish kunlari | AOE
                  mehmonxonalari
                </h3>
              </div>
            </div>
          </div>
        </div>
        <div className="detailed-content">
          <Tabs>
            <div className="row tour-single-rise">
              <main className="col-md-9">
                <div className="tours-tabs">
                  <TabList className="nav nav-tabs hidden-xs">
                    {DetaileDate.map((item, i) => (
                      <Tab>
                        <button
                          className={activeTab === i ? "active" : ""}
                          onClick={() => handleLink(i)}
                        >
                          {item.headerTitle}{" "}
                        </button>
                      </Tab>
                    ))}
                  </TabList>
                </div>
                <div className="tab-content hidden-xs">
                  <TabPanel>
                    <div className="tab-pane active">
                      <div className="tours-tabs__info">
                        <div className="tours-tabs__info__item">
                          <div className="tours-tabs__info__item__content d-flex">
                            <div className="tours-tabs__info__item__icon">
                              <Ri24HoursFill />
                            </div>
                            <div className="mx-4">
                              <div className="tours-tabs__info__item__title">
                                8 kun 7 kecha
                              </div>
                              <div className="tours-tabs__info__item__description">
                                Kunlar soni
                              </div>
                            </div>
                          </div>
                        </div>{" "}
                        <div className="tours-tabs__info__item">
                          <div className="tours-tabs__info__item__content d-flex">
                            <div className="tours-tabs__info__item__icon">
                              <IoManSharp />
                            </div>
                            <div className="mx-4">
                              <div className="tours-tabs__info__item__title">
                                0+{" "}
                              </div>
                              <div className="tours-tabs__info__item__description">
                                Yoshi
                              </div>
                            </div>
                          </div>
                        </div>{" "}
                        <div className="tours-tabs__info__item">
                          <div className="tours-tabs__info__item__content d-flex">
                            <div className="tours-tabs__info__item__icon">
                              <GoCalendar />
                            </div>
                            <div className="mx-4">
                              <div className="tours-tabs__info__item__title">
                                Aprel  Oktyabr
                              </div>
                              <div className="tours-tabs__info__item__description">
                                Mavsum
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div className="tours-tabs__content padding-all">
                        <h2>
                          <strong>Manzali sayohat</strong>
                        </h2>
                        <p>
                          <span></span>
                        </p>
                        <table
                          style={{ height: "233px", width: "844px" }}
                          className="table table-bordered tours-tabs__table"
                        >
                          <tbody>
                            <tr>
                              <td style={{ width: "184px" }}>
                                <strong>JONAB KETISH</strong>
                              </td>
                              <td style={{ width: "660px" }}>Seshanba, Juma</td>
                            </tr>
                            <tr>
                              <td style={{ width: "184px" }}>
                                <strong>QAYTIB KELISH</strong>
                              </td>
                              <td style={{ width: "660px" }}>Seshanba, Juma</td>
                            </tr>{" "}
                            <tr>
                              <td style={{ width: "184px" }}>
                                <strong>VIZA</strong>
                              </td>
                              <td style={{ width: "660px" }}>
                                Talab qilinmaydi, xorijga chiqish pasporti,
                                uchib ketishdan 72 soat oldin qilingan PZR test
                                zarur
                              </td>
                            </tr>
                          </tbody>
                        </table>
                        <p>
                          <strong>
                            DASTUR BOYICHA QATNOVLAR: Dushanba / Juma
                          </strong>
                        </p>
                        <p>
                          <strong>&nbsp;</strong>
                        </p>
                        <p>
                          <strong>NARXGA KIRITILGAN:</strong>
                        </p>
                        <ul className="included-ul-content">
                          <li>
                            <img src={travel} alt="" />{" "}
                            aeroport-mehmonxona-aeroport guruh bolib transferi
                          </li>
                          <li>
                            <img src={travel} alt="" /> nonushtalarni oz ichiga
                            oluvchi mehmonxonada 3 kecha yashash
                          </li>
                          <li>
                            <img src={travel} alt="" /> guruh bolib piyoda
                            sayohat
                          </li>
                        </ul>
                        <p>&nbsp;</p>
                        <h5>
                          <strong>Sayohat narxiga kiritilmagan:</strong>
                        </h5>
                        <ul className="extra">
                          <li>
                            <img src={travel} alt="" />
                            Aviaparvoz (Kattalar{" "}
                            <strong>4.770.000 somdan boshlab</strong>; Bolalar{" "}
                            <strong> 4.055.000 somdan boshlab</strong>)
                          </li>{" "}
                          <li>
                            <img src={travel} alt="" />
                            Tibbiy sugurta (+COVID-19 kiritilgan) sayohat
                            davrida 1 kishi uchun
                            <strong>35.000 som </strong>(oilalarga chegirmalar
                            taqdim qilinadi!)
                          </li>{" "}
                          <li>
                            <img src={travel} alt="" />
                            Firma xizmatlari 1 kishi uchun
                            <strong>200 000 som</strong>
                          </li>
                          <li>
                            <img src={travel} alt="" />
                            Shaxsiy xarajatlar
                          </li>
                        </ul>
                        <p>
                          <strong>
                            P.S. Istalgan sayyohlar soni uchun individual
                            hisob-kitob qilish imkoniyati bor.
                          </strong>
                        </p>
                        <p>
                          <strong>
                            Narxlar taxminiy korsatilgan va tolov vaqtidagi
                            kursga qarab ozgarishi mumkin.
                          </strong>
                        </p>
                        <p>&nbsp;</p>
                        <p>
                          <strong>
                            Sayyohlik firmasi dasturga ozgartirishlar va/yoki
                            qoshimchalar kiritish, turlar ketma-ketligi va
                            kunlarini ozgartirish huquqini ozida saqlab
                            qoladi.
                          </strong>
                        </p>
                      </div>
                    </div>{" "}
                  </TabPanel>
                  <TabPanel>
                    <div className="tab-pance">
                      <div className="tours-tabs__content padding-all">
                        <div className="timeline">
                          <div className="timeline__item">
                            <div className="timeline__item__icon-wrap">
                              <div className="timeline__item__icon">
                                <div className="timeline__item__icon__text">
                                  1
                                </div>
                              </div>
                            </div>
                            <div className="timeline__item__content padding-left">
                              <h3 className="timeline__item__title">1-kun</h3>
                              <div className="timeline__item__description">
                                <p>
                                  Istanbulga uchib kelish. Aeroportdan
                                  mehmonxonaga transfer.
                                </p>
                                <p>Mehmonxonaga joylashish, bosh vaqt.</p>
                                <p>
                                  Xohishga qarab qoshimcha tolov
                                  evaziga:Xohishga qarab qoshimcha tolov
                                  evaziga:
                                </p>
                                <p>
                                  Bosforga ajoyib manzara ochiluvchi Galata
                                  koprigidagi baliq restoranida kechki ovqat.
                                </p>
                                <p>
                                  Tungi Bosfor boylab kema sayohati (shou
                                  dastur, kechki ovqat, spirtli/spirtsiz
                                  ichimliklar, mehmonxonadan/ga transfer)
                                </p>
                              </div>
                            </div>
                          </div>{" "}
                          <div className="timeline__item">
                            <div className="timeline__item__icon-wrap">
                              <div className="timeline__item__icon">
                                <div className="timeline__item__icon__text">
                                  2
                                </div>
                              </div>
                            </div>
                            <div className="timeline__item__content padding-left">
                              <h3 className="timeline__item__title">2-kun</h3>
                              <div className="timeline__item__description">
                                <p>Mehmonxonada nonushta.</p>
                                <p>
                                  Bosfor panoramasi + Lolalar festivali guruh
                                  bolib ekskursiyasi
                                </p>
                                <p>14:30 Turning boshlanishi</p>
                                <p>
                                  Bosfor boylab kemada sayohat, Qizlar minorasi
                                  (tashqaridan), Rumeli Hisor qalasi
                                  (tashqaridan), Bosfor orqali koprik, Amirxon
                                  parki (lolalar festivali)
                                </p>
                                <p>
                                  18:00 Sayohatning yakunlanishi, mehmonxonaga
                                  transfer.
                                </p>
                              </div>
                            </div>
                          </div>{" "}
                          <div className="timeline__item">
                            <div className="timeline__item__icon-wrap">
                              <div className="timeline__item__icon">
                                <div className="timeline__item__icon__text">
                                  3
                                </div>
                              </div>
                            </div>
                            <div className="timeline__item__content padding-left">
                              <h3 className="timeline__item__title">3-kun</h3>
                              <div className="timeline__item__description">
                                <p>Mehmonxonada nonushta.</p>
                                <p>
                                  Bosh kun. Shopping va qoshimcha
                                  ekskursiyalar uchun imkoniyat
                                </p>
                              </div>
                            </div>
                          </div>{" "}
                          <div className="timeline__item">
                            <div className="timeline__item__icon-wrap">
                              <div className="timeline__item__icon">
                                <div className="timeline__item__icon__text">
                                  4
                                </div>
                              </div>
                            </div>
                            <div className="timeline__item__content padding-left">
                              <h3 className="timeline__item__title">4-kun</h3>
                              <div className="timeline__item__description">
                                <p>
                                  Mehmonxonada nonushta. Aeroportga transfer,
                                  Istanbuldan uchib ketish
                                </p>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </TabPanel>
                  <TabPanel>
                    <div className="tours-tabs__content padding-all">
                      <p>
                        <strong>ISTANBUL</strong>  sirli va muhtasham,
                        sershovqin va ertaknamo gozal Istanbul  bu Yevropa va
                        Osiyo, ananaviy Sharq va zamonaviy Garb ortasidagi
                        koprik. Ozining boy tarixi davomida u nomini bir necha
                        marta ozgartirishga va Rim, Vizantiya, Usmonlilar va
                        Lotin imperiyalarining poytaxti bolishga ulgurdi.
                      </p>
                      <p>
                        <img
                          src={Istanbul}
                          alt=""
                          className="size-full wp-image-2463 aligncenter"
                          width="800"
                          height="450"
                        />
                      </p>
                      <p>
                        <strong>KAPPADOKIYA</strong>  siz geografik xaritadan
                        topa olmaydigan sirli joy. Antik davrga borib taqaluvchi
                        nom va ushbu provinsiyani orab olgan sir-u sinoat
                        tarixning ajralmas qismi hisoblanadi. Million yillar
                        avval vulqondan chiqqan lava va tabiiy jarayonlar
                        tufayli bu yerda noyob, ozga sayyoranikiga oxshash,
                        minglab tosh qoziqorinlar va turli xil rang hamda
                        olchamdagi haykallardan iborat manzara yuzaga keldi.
                        Mahalliy aholi orasida keng tarqalgan rivoyatga kora,
                        bu yerda farishtalar yashaydi, deb hisoblanadi.
                        Kappadokiyadagi har bir yil  bu osmon ostidagi noyob,
                        oziga xos muzey.
                      </p>
                      <p>
                        <img
                          src={kappadokiya}
                          alt=""
                          className="size-full wp-image-2791 aligncenter"
                        />
                      </p>
                      <p>
                        Kappadokiyaga borganda beixtiyor boshqa sayyoraga tushib
                        qolgandek fikr uygonadi  koz ongingizda namoyon
                        boluvchi landshaftlar shu qadar betakror va hech
                        narsaga oxshamaydi. Bu yerda uchini qor qoplagan
                        vulqonlar, baland bolmagan, alohida turgan toglar,
                        qoya massivlari va farovon vodiylar bor.
                      </p>
                    </div>
                  </TabPanel>
                  <TabPanel>
                    <div className="tours-tabs__content padding-all">
                      <p>
                        1 KISHI UCHUN NARX (2 KISHILIK GURUH): 17 000 000
                        somdan boshlab
                      </p>
                      <p>
                        1 KISHI UCHUN NARX (4 KISHILIK GURUH): 8 500 000 somdan
                        boshlab
                      </p>
                      <p>
                        1 KISHI UCHUN NARX (6 KISHILIK GURUH): 6 500 000 somdan
                        boshlab
                      </p>
                    </div>
                  </TabPanel>
                  <TabPanel>
                    <div className="tours-tabs__content padding-all">
                      <div className="row product-thumbnails">
                        <div className="col-md-12">
                          <a
                            className="woocommerce-main-image swipebox"
                            href="https://travelsystem.uz/wp-content/uploads/2021/06/turkiya-osmani.jpg"
                            title="Usmonli sultonlar va ularning lolalari"
                          >
                            <img
                              width="1920"
                              href="800"
                              src={dubai}
                              alt=""
                              className="attachment-full size-full wp-post-image"
                            />
                          </a>
                        </div>
                      </div>
                      <div className="product-thumbnails columns-3">
                        <div className="col-sm-3 col-xs-4 product-thumbnails__item">
                          <a href="https://travelsystem.uz/wp-content/uploads/2021/06/tur-v-stambul-iz-tashkenta16-1-180x120.jpg">
                            <img src="" alt="" />
                          </a>
                        </div>
                        <div className="row product-item-foto">
                          <div className="col-6">
                            <LazyLoadImage
                              className="resultImage"
                              src={item1}
                              effect="blur"
                              delayTime="300"
                              height="150"
                            />
                          </div>
                          <div className="col-6">
                            <LazyLoadImage
                              className="resultImage"
                              src={item2}
                              effect="blur"
                              delayTime="300"
                            />
                          </div>
                          <div className="col-6">
                            <LazyLoadImage
                              className="resultImage"
                              src={item3}
                              effect="blur"
                              delayTime="300"
                            />
                          </div>
                          <div className="col-6">
                            <LazyLoadImage
                              className="resultImage"
                              src={item4}
                              effect="blur"
                              delayTime="300"
                            />
                          </div>
                        </div>
                      </div>
                    </div>
                  </TabPanel>
                </div>
              </main>
              <aside className="col-md-3">
                <div className="order-container">
                  <div className="order-content">
                    <button className="btn-order" onClick={openModal}>
                      BUYURTMA QILISH
                      <i className="order-icon"></i>
                    </button>
                    <div className="price-decoration block-after-indent mt-3">
                      <div className="price-description-content text-center">
                        <span className="woocommerce-Price-amount amount">
                          1 687 000 so'mdan
                        </span>
                      </div>
                      <div className="price-decoration__label">
                        <p>Bir kishi uchun</p>
                      </div>
                    </div>
                  </div>
                </div>
              </aside>
            </div>
          </Tabs>
        </div>
      </div>
      <div className="modal">
        <Modal
          isOpen={modalIsOpen}
          onAfterOpen={afterOpenModal}
          onRequestClose={closeModal}
          style={customStyles}
          contentLabel="Example Modal"
        >
          <div className="modal-contain p-1">
            <div className="form-contact__fields-short">
              <div className="d-flex">
                <h6 className="text-center">HOZIR BUYURTMA BERING !</h6>{" "}
                <span onClick={closeModal} className="close-btn">
                  <Close />
                </span>
              </div>
              <div className="form-contact__item-short">
                <span className="form-control-wrap">
                  <input
                    type="text"
                    className="modal-form-input"
                    placeholder="Ism"
                  />
                </span>
              </div>{" "}
              <div className="form-contact__item-short">
                <span className="wpcf7-form-control-wrap your-name">
                  <input
                    type="text"
                    className="wpcf7-form-control wpcf7-text wpcf7-validates-as-required form-validation-item"
                    placeholder="Familya"
                  />
                </span>
              </div>
            </div>
            <div className="form-contact__fields-short">
              <div className="wpcf7-form-control-wrap your-mail">
                <input
                  type="email"
                  className="wpcf7-form-control wpcf7-text wpcf7-email wpcf7-validates-as-required wpcf7-validates-as-email form-validation-item"
                  placeholder="Sizning Email"
                />
              </div>
            </div>{" "}
            <div className="form-contact__fields-short">
              <div className="wpcf7-form-control-wrap your-mail">
                <input
                  type="number"
                  className="wpcf7-form-control wpcf7-text wpcf7-email wpcf7-validates-as-required wpcf7-validates-as-email form-validation-item"
                  placeholder="Telefon raqami"
                  data-mask-pattern="+380 (__) ___-____"
                />
              </div>
            </div>
            <p>
              <span className="atbtn atbtn--input-wrap text-center justify-content-center m-auto">
                <i className="atbtn__icon fa fa-paper-plane"></i>
                <input
                  type="submit"
                  value="Buyurtma qilish"
                  className="modal-button"
                />
              </span>
            </p>
          </div>
        </Modal>
      </div>
      <Footer />
    </>
  );
};

export default Detailed;
